/* Story Card */

.content {
  display: flex;
  flex-wrap: wrap;
}

.main {
  margin-bottom: 190px;
}

.stories {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 0px;
  margin: 0px;
  list-style-type: none;
}

/* Filter */

.filter {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 82px;
  width: 100%;
}

.filter-category {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  font-family: "Rubik";
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: #131415;
  width: 200px;
}

.filter select {
  display: block;
  height: calc(1.5em + 0.75rem + 2px);
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #495057;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.filter select:hover {
  color: #131415;
  background: #fff;
  border: 1px solid #d73728;
  cursor: pointer;
}

.storyCard {
  display: flex;
  width: 550px;
  height: 243px;
  border: 1px solid #e6e6e6;
  box-sizing: border-box;
  border-radius: 8px;
}

.featureImage {
  width: 217px;
  border-radius: 8px 0px 0px 8px;
  object-fit: cover;
}

.featureTitle {
  font-family: "Rubik";
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
}

.featureContent {
  font-family: "Roboto";
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
}

.featureDate,
.featureCity {
  font-family: "Rubik";
  font-weight: 500;
  font-size: 12px;
  line-height: 15px;
  color: #747b80;
}

